.App {
  text-align: center;
  background-color: #026f6e;
  width: 400px;
  height: 580px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

h1{
  padding-top: 25px;
}

input{
  width: 300px;
  height: 35px;
  border: none;
  outline: none;
  border-radius: 8px;
  color: #fff;
  background-color: #160d18;
  font-size: 18px;
}

.calculadora{
  max-width: 299px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 25px;
}

.calculadora button{
  background-color: #160d18;
  color: #fff;
  font-size: 22px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  cursor: pointer;
  border: none;
  margin: 5px;
}

.calculadora svg{
  font-size: 15px;
}
